import React from "react";
import { CCard, CTooltip } from "@coreui/react";
import BootstrapTable from "react-bootstrap-table-next";
import { validateSingle } from "Component/FileUploader/services.js";
import "./bootstrapTable-style.scss";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import { validatePoSkuInfoId } from "apiServices";
import axios from "axios";

//cell editable
//doc : https://react-bootstrap-table.github.io/react-bootstrap-table2/storybook/index.html?selectedKind=Cell%20Editing&selectedStory=Click%20to%20Edit&full=0&addons=1&stories=1&panelRight=0&addonPanel=storybook%2Factions%2Factions-panel
import cellEditFactory from "react-bootstrap-table2-editor";


class TableEditable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      validateStatus: false,
      validateResult: "Please Wait...",
      // globalData: this.props.globalData,
      rowCount: 0,
      keyy: 0,
    };
  }


  validatePOSKU = async (id, row, idColumn) => {
    const { setCsvDataTmp, globalData, setIsValidate } = this.props;
    let tmpdata = globalData;
    try {
      setIsValidate(false)
      const url = "/stocktake/rowValidationUpload";
      let elem = null;
      let elemspan = null;
      setTimeout(async () => {
        elem = document.getElementById(`${idColumn}`);
        elemspan = document.getElementById(`${idColumn}span`)
        if (elem === null || elem === undefined) {
          return 0;
        }
        elemspan.innerHTML = "Validating"
        await axios({
          method: "post",
          url: url,
          data: { data: [id] },
        }).then(function (response) {
          let data = response?.data?.data;
          var index = tmpdata.findIndex(obj => obj.id == row);
          if (data[0] && data[0].isExist === true) {
            tmpdata[index].wmsPoSkuCode = data[0].wmsPoSkuCode;
            tmpdata[index].wmsPoSkuCodeStatus = true;
            tmpdata[index].wmsPoSkuCodeReason = "PO SKU Code Not Found";
            tmpdata[index].expiryDate = data[0].expiryDate;
            tmpdata[index].inboundDate = data[0].inboundDate;
            tmpdata[index].supplierName = data[0].supplierName;
            tmpdata[index].supplierUuid = data[0].supplierUuid;
            tmpdata[index].uom = data[0].uom;
            tmpdata[index].photos = data[0].photos;
            elem.classList.remove("red");
            elem.classList.add("hiddenTooltip")
          } else {
            tmpdata[index].wmsPoSkuCodeStatus = false;
            tmpdata[index].wmsPoSkuCodeReason = "PO SKU Code Not Found";
            tmpdata[index].expiryDate = "";
            tmpdata[index].inboundDate = "";
            tmpdata[index].supplierName = "";
            tmpdata[index].supplierUuid = "";
            elem.classList.add("red");
            elem.classList.remove("hiddenTooltip")
            elemspan.innerHTML = "PO SKU Code Not Found"
          }
          setCsvDataTmp(tmpdata);
        });
      }, 400);
    } catch (error) {
      return false;
    }
  };

  validateOutbountReason = (value, row, idColumn) => {
    const { globalData } = this.props;
    let tmp_data = globalData
    var index = tmp_data.findIndex(obj => obj.id == row);
    let datax = tmp_data[index];
    if (value == '' && datax['outboundType'].toUpperCase() == "LAINNYA") {
      setTimeout(() => {
        let elem = document.getElementById(`row${row}-outboundReason`);
        elem.classList.add("red", "tooltip-custom");
        elem.innerHTML = `Outbound Reason Can't be empty<span class="tooltiptext">Outbound Reason Can't be empty if Outbound type = 'Lainnya'</span>`;

      }, 300);
    }
  }

  validateOutbountType = (value, row, idColumn) => {
    const { globalData, setCsvDataTmp, setIsValidate } = this.props;
    let tmp_data = globalData
    setIsValidate(false)
    if (value.toUpperCase() !== "LAINNYA") {
      setIsValidate(true)
      return 0;
    }

    var index = tmp_data.findIndex(obj => obj.id == row);
    let datax = tmp_data[index];
    if (datax['outboundReason'] == "") {
      setTimeout(() => {
        let elem = document.getElementById(`row${row}-outboundReason`);
        elem.classList.add("red", "tooltip-custom");
        elem.innerHTML = `Outbound Reason Can't be empty<span class="tooltiptext">Outbound Reason Can't be empty if Outbound type = 'Lainnya'</span>`;
      }, 300);
    }
  }

  validateSKUNumber = async (id, row, idColumn, key) => {
    const { globalData, setCsvDataTmp, setIsValidate } = this.props;
    let tmp_data = globalData
    try {
      setIsValidate(false)
      const url = "/oss/skuByNumbers";
      let elem = null;
      let elemspan = null;
      setTimeout(async () => {
        elem = document.getElementById(`${idColumn}`);
        elemspan = document.getElementById(`${idColumn}span`)
        if (elem === null || elem === undefined) {
          return 0;
        }
        elemspan.innerHTML = "Validating"
        await axios({
          method: "post",
          url: url,
          data: { skuList: [id] },
        }).then(function (response) {
          let data = response?.data?.data;
          let isSuccess = response.data.isSuccess
          var index = tmp_data.findIndex(obj => obj.id == row);
          if (isSuccess === true && data.length > 0) {
            tmp_data[index][key] = data[0].skuNumber;
            tmp_data[index][key + 'Reason'] = data[0].skuDescription;
            tmp_data[index][key + 'Status'] = true;
            tmp_data[index][key + 'Uuid'] = data[0].id;
            elem.classList.remove("red");
            elem.classList.add("hiddenTooltip")

            //custom 
            if (key == 'skuNumber') {
              tmp_data[index].skuUuid = data[0].id;
              tmp_data[index].photo = data[0].imageMd;
            }


            if (key == 'newGrade') {
              tmp_data[index].newGradeUom = data[0].uom;
            }

          } else {
            tmp_data[index][key] = id;
            tmp_data[index][key + 'Reason'] = "SKU Number Not Found";
            tmp_data[index][key + 'Status'] = false;
            tmp_data[index][key + 'Uuid'] = null;
            elem.classList.add("red");
            elem.classList.remove("hiddenTooltip")
            elemspan.innerHTML = "SKU Number Not Found"

            if (key == 'skuNumber') {
              tmp_data[index].skuUuid = "";
              tmp_data[index].photo = "";
            }
            if (key == 'newGrade') {
              tmp_data[index].newGradeUom = null;
            }
          }
          setCsvDataTmp(tmp_data);
        });
      }, 400);
    } catch (error) {
      return false;
    }
  };

  render() {
    const {
      columns,
      defaultSorted,
      hiddenRows,
      tableClass,
      warehouseArea,
      outboundType,
      setIsValidate,
      setCheckingCsv,
      globalData,
      editable = true
    } = this.props;
    let { validateStatus, rowCount, keyy } = this.state;
    validateStatus = true;
    //wh area
    let wh_area_label = warehouseArea.map((data) => data.label.toUpperCase()) || [];
    let wh_area_value = warehouseArea.map((data) => data.value) || [];
    let wh_outboundtype_label = !outboundType ? [] : outboundType.map((data) => data.label.toUpperCase());
    let wh_outboundtype_value = !outboundType ? [] : outboundType.map((data) => data.value);
    const newFormat = columns.map((data, index) => {

      data.validator = (newValue, row, column, done) => {
        let key = data.dataField;
        let keyStatus = key + "Status";
        let keyReason = key + "Reason";
        let stat = true;
        let reason = null;
        let idColumn = 'row' + row['id'] + '-' + key;
        if (key == "wmsPoSkuCode") {
          this.validatePOSKU(newValue, row.id, idColumn, key);
          stat = true;
        }

        if (key == "skuNumber") {
          this.validateSKUNumber(newValue, row.id, idColumn, key);
          stat = true;
        }


        let arr_grade = ['WASTE', 'SEMI WASTE'];
        if (key == "newGrade" && this.props.transaction == "waste") {
          if (!arr_grade.includes(newValue.toUpperCase())) {
            reason = "Waste Type not found";
            checkType = false;
            stat = false;
          } else {
            row['newGradeUuid'] = newValue
            row['newGradeUom'] = null
          }
        } else if (key == "newGrade") {
          if (!arr_grade.includes(newValue.toUpperCase())) {
            //if not waste or semi waste 
            if (parseInt(newValue)) {
              //if number
              stat = true;
              this.validateSKUNumber(newValue, row.id, idColumn, key);
            } else if (newValue == "") {
              row['newGradeUuid'] = null
              row['newGradeUom'] = null
              stat = true;
            } else {
              //if not number => undefined
              reason = "Grade Undefined";
              checkType = false;
              stat = false;
            }
          } else {
            row['newGradeUuid'] = newValue
            row['newGradeUom'] = null
          }
        }


        if (key == "outboundType" && wh_outboundtype_value.length > 0) {
          if (!wh_outboundtype_label.includes(newValue.toUpperCase())) {
            stat = false;
            reason = `Outbound Type Not Found`;
          } else {
            let i = wh_outboundtype_label.indexOf(newValue.toUpperCase());
            row["outboundTypeCode"] = wh_outboundtype_value[i];
            stat = true;
            this.validateOutbountType(newValue, row.id, idColumn)
          }
        }

        if (key == "outboundReason") {
          this.validateOutbountReason(newValue, row.id, idColumn)
        }

        if (key == "areaName" || key == "areaNameDestination") {
          if (!wh_area_label.includes(newValue.toUpperCase())) {
            stat = false;
            reason = `Warehouse Area Not Found`;
          } else {
            let i = wh_area_label.indexOf(newValue.toUpperCase());
            if(key == "areaNameDestination"){
              row["areaDestination"] = wh_area_value[i];
            }else{
              row["areaSource"] = wh_area_value[i]; 
            }
            stat = true;
          }
        }

        if (key == "newPack") {
          let checkType = isNaN(newValue) ? false : true;
          if (!checkType) {
            stat = false;
            reason = `Pack Should be a number`;
          } else { 
            stat = true;
          }
        }

        let checkType = null;
        if (data.type == "number") {
          checkType = isNaN(newValue) ? false : true;
        } else {
          checkType = true;
        }
        if (checkType === false) {
          reason = `${data.text} Should be a ${data.type}`;
        }

        //some field can null
        let canNull = ['grade', 'outboundReason', 'ripenessLevel', 'newRipeness', 'reason', 'expiryDate']
        if (canNull.includes(key) && newValue === "") {
          reason = "";
          checkType = true;
          stat = true;
        } else if (key == "grade") {
          let arr_grade = ['WASTE', 'SEMI WASTE'];
          if (!arr_grade.includes(newValue.toUpperCase())) {
            reason = "Waste Type Not Found";
            checkType = false;
            stat = false;
          }
        }

        //check ripenesslevel
        if (key == "ripenessLevel" || key == "newRipeness") {
          let allowedRipenessLevel = [0, 1, 2, 3]
          if (allowedRipenessLevel.includes(parseInt(newValue)) || newValue == "") {
            checkType = true;
            stat = true;
            reason = "";
            if (newValue == "") {
              newValue = ""
            }
          } else {
            checkType = false;
            stat = false;
            reason = "Undefined Ripeness Level";
          }
        }

        //check date
        if (data.type == "date_indo") {
          //checking date
          let regex_date = /(0[1-9]|[12][0-9]|3[01])[-.](0[1-9]|1[012])[-.](19|20)\d\d/;
          if (newValue.match(regex_date)) {
            checkType = true;
            stat = true;
          } else if (newValue == "") {
            checkType = true;
            stat = true;
          } else {
            reason = `Date format must be 'dd-mm-yyyy'`;
            checkType = false;
            stat = false;
          }
        }

        //set notif
        row[keyReason] = reason;
        if (stat && checkType) {
          let specialKey = ["wmsPoSkuCode", "skuNumber"]
          if (!specialKey.includes(key)) {
            //some key have their own checking, so if not in array do checking error
            row[keyStatus] = true;
            setCheckingCsv();
          }
        }
        return {
          valid: stat && checkType ? true : false,
          message: reason,
        };
      };

      data.formatter = (cell, row, index) => {
        let key = data.dataField;
        let keyStatus = key + "Status";
        let keyReason = key + "Reason";
        let stat = true;
        let idColumn = 'row' + row['id'] + '-' + key;
        let txt = row[keyReason];

        if (row[keyStatus] === false) {
          validateStatus = false;
          stat = false;
          return (
            <div id={idColumn} className="tooltip-custom red">
              {cell}
              <span id={idColumn + 'span'} className="tooltiptext">{txt}</span>
            </div>
          );
        }

        return (<div id={idColumn} className="tooltip-custom hiddenTooltip">
          {cell}
          <span id={idColumn + 'span'} className="tooltiptext">{txt}</span>
        </div>);
      };
      return data;
    });

    const sortOption = {
      // No need to configure sortFunc per column
      sortFunc: this.sortFunc,
      // No need to configure sortCaret per column
      sortCaret: (order, column) => {
        if (!order) return <i className="iconU-tableSortingNull ml-2"></i>;
        else if (order === "asc")
          return <i className="iconU-tableSortingTop ml-2"></i>;
        else if (order === "desc")
          return <i className="iconU-tableSortingBottom ml-2"></i>;
        return null;
      },
    };
    console.clear()
    console.log(globalData)
    return (
      <CCard className="table-responsive">
        <BootstrapTable
          keyField="id"
          data={globalData}
          columns={newFormat}
          sort={sortOption}
          classes={tableClass || null}
          cellEdit={(editable === true)
            ? cellEditFactory({ mode: "click", blurToSave: true, })
            : null
          }
          bordered={false}
          hiddenRows={hiddenRows}
          key={keyy}
        />
      </CCard>
    );
  }
}



class TableResult extends React.Component {
  render() {
    const {
      columns,
      tableClass,
      data
    } = this.props;

    const newFormat = columns.map((data, index) => {
      data.formatter = (cell, row, index) => {
        let key = data.dataField;
        if (key == 'uploadStatus' && row['uploadStatus'] === "Failed") {
          return (
            <div className="tooltip-custom red">
              {cell}
              <span className="tooltiptext">{row['uploadStatusReason']}</span>
            </div>
          );
        } else if (key == 'uploadStatus' && row['uploadStatus'] === "Success") {
          return (
            <div className="green">
              {cell}
              <span></span>
            </div>
          );
        }

        return (<div >
          {cell}
          <span></span>
        </div>);
      };
      return data;
    });

    const sortOption = {
      // No need to configure sortFunc per column
      sortFunc: this.sortFunc,
      // No need to configure sortCaret per column
      sortCaret: (order, column) => {
        if (!order) return <i className="iconU-tableSortingNull ml-2"></i>;
        else if (order === "asc")
          return <i className="iconU-tableSortingTop ml-2"></i>;
        else if (order === "desc")
          return <i className="iconU-tableSortingBottom ml-2"></i>;
        return null;
      },
    };

    return (
      <CCard className="table-responsive">
        <BootstrapTable
          keyField="id"
          data={data}
          columns={newFormat}
          sort={sortOption}
          classes={tableClass || null}
          bordered={false}
        />
      </CCard>
    );
  }
}


export { TableResult, TableEditable };
